.footer {
    background-color: #1A2028;
}

.footer-container {
    margin: 0 auto;
}

.footer-container-title {
    color: #E2E3E4;
    text-decoration: none;
}

.footer-container-button {
    color: #E2E3E4;
    text-decoration: none;
    border: 1px solid #E2E3E4;
}

.footer-container-link {
    text-decoration: none;
    color: #E2E3E4;
}

.footer-container ul li {
    display: block;
}

.footer-container-la {
    color: #E2E3E4;
}

@media screen and (max-width: 2560px) {
    .footer {
        height: 333px;
        margin: 50px 0 0 0;
    }

    .footer-container {
        width: calc(100% - 200px);
    }

    .footer-container-title {
        line-height: 100px;
    }

    .footer-container-button {
        padding: 10px 50px 10px 50px;
        margin: 30px 8px 0 0;
    }

    .footer-container ul li {
        margin: 20px 0 20px 0;
    }

    .footer-container-la {
        margin: 50px 0 0 0;
        font-size: 12px;
    }

    .footer-container-ig {
        margin: -8px 0 0 10px;
    }
}

@media screen and (max-width: 1440px) {
    .footer {
        height: 333px;
        margin: 50px 0 0 0;
    }

    .footer-container {
        width: calc(100% - 200px);
    }

    .footer-container-title {
        line-height: 100px;
    }

    .footer-container-button {
        padding: 10px 50px 10px 50px;
        margin: 30px 8px 0 0;
    }

    .footer-container ul li {
        margin: 20px 0 20px 0;
    }

    .footer-container-la {
        margin: 50px 0 0 0;
        font-size: 12px;
    }

    .footer-container-ig {
        margin: -8px 0 0 10px;
    }
}

@media screen and (max-width: 682px) {
    .footer-container {
        width: calc(100% - 100px);
    }
}

@media screen and (max-width: 444px) {
    .footer-container {
        width: calc(100% - 50px);
    }

    .footer-container-em {
        display: none;
    }
}