
nav {
    margin: 0 auto;
}

li {
    display: inline;
}

.nav-link, .nav-title, .nav-tagline {
    color: #E2E3E4;
    text-decoration: none !important;
}

.nav-tagline {
    line-height: normal;
}

.nav-title-strikethrough,
.nav-link-strikethrough1,
.nav-link-strikethrough2 {
    position: relative;
}

@media screen and (max-width: 2560px) {
    nav {
        height: 100px;
        width: calc(100% - 200px);
    }

    .nav-link, .nav-title {
        line-height: 100px;
    }

    .nav-link {
        margin: 0 25px 0 0;
    }

    .nav-tagline {
        width: 300px;
        margin: 40px 100px 0 0;
    }

    .nav-title-strikethrough::after {
        content: '';
        position: absolute;
        top: 50px;
        left: -6px;
        width: 0px;
        height: 2px;
        background: #E2E3E4;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -ms-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
        }

    .nav-title-strikethrough:hover::after {
        width: 55px;
    }

    .nav-link-strikethrough1::after {
        content: '';
        position: absolute;
        top: 10px;
        left: -6px;
        width: 0px;
            height: 2px;
        background: #E2E3E4;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -ms-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
    }

    .nav-link-strikethrough1:hover::after {
        width: 75px;
    }

    .nav-link-strikethrough2::after {
        content: '';
        position: absolute;
        top: 10px;
        left: -6px;
        width: 0px;
        height: 2px;
        background: #E2E3E4;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -ms-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
        }

    .nav-link-strikethrough2:hover::after {
        width: 59px;
    }
}

