@media screen and (max-width: 964px) {
    .nav-tagline {
        display: none;
    }
}

@media screen and (max-width: 682px) {
    nav {
        width: calc(100% - 100px);
    }
}

@media screen and (max-width: 444px) {
    nav {
        height: 75px;
        width: calc(100% - 50px);
    }

    .nav-link, .nav-title {
        line-height: 75px;
    }

    .nav-title {
        display: none;
    }
}