.home-title {
    margin: 0 auto;
}

.home-tagline {
    color: #E2E3E4;
}

@media screen and (max-width: 2560px) {
    .home {
        height: 636px; /* .home-title height + .home-resume height */
    }

    .home-title {
        font-size: 25vw;
        width: calc(100% - 200px);
    }

    .home-tagline {
        display: none;
    }

    .home-resume {
        width: calc(100% - 200px);
        height: 200px;
        margin: 75px auto;
    }

    .home-resume-button {
        display: block;
        height: 100%;
        width: 100%;
        text-align: center;
        border: 1px solid #E2E3E4;
        line-height: 200px;
        color: #E2E3E4;
        text-decoration: none;
        font-size: 48px;
        font-weight: 700;
    }

    .home-resume-button::after {
        content: '';
        position: absolute;
        width: 0;
        height: 3px;
        margin: 102px 0 0 -360px;
        background: #E2E3E4;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -ms-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
    }

    .home-resume-button:hover::after {
        width: 384px;
    }
}

